import React, { Component, Fragment } from 'react'
import Select from 'react-select'

export default class CustomSelect extends Component {
  render() {
    const { options = [], label, value } = this.props
    return (
      <div>
        <label htmlFor={label}>{label}</label>
        <div style={{
          marginTop: 5,
          width: 330
        }}>
          <Select 
            defaultValue={value}
            options={options} 
            />
        </div>
      </div>
    )
  }
}
