import React, { Component } from 'react'
import Form from '../../../widgets/Form'
import TextField from '../../../widgets/TextField'
import CLIENT_SETTINGS from './ClientSettings'
import Select from '../../../widgets/Select'

const settingOptionsToSelectOptions = (settingOptions) => {
  // { value: 'true', flag: 'console', label: 'Yes' },
  const options = settingOptions.map(o => {
    const { value, flag, label } = o
    return { label, value }
  })
  return options
}

export default class ClientConfiguration extends Component {
  handleChange = () => {

  }
  renderField(setting) {
    let { id, type, label, default: defaultValue,  disabled = false } = setting
    if (setting.options) {
      type = 'select'
    }
    switch (type) {
      case 'select': {
        const options = settingOptionsToSelectOptions(setting.options)
        const defaultOption = options.find(o => o.value === defaultValue)
        return (
          <Select
            key={id}
            data-test-id={`input-select-${id}`}
            label={label}
            value={defaultOption}
            options={options}
            disabled={disabled}
            onChange={value => this.handleChange(id, value)}
          />
        )
      }
      default:
        return (
          <TextField
            key={id}
            data-test-id={`input-text-${id}`}
            variant="outlined"
            label={label}
            value={defaultValue}
            disabled={disabled}
            onChange={event => this.handleChange(id, event.target.value)}
            fullWidth
          />
        )
    }
  }
  render() {

    return (
      <div style={{
        flex: 1,
      }}>
        <Form style={{
          flexDirection: 'row',
          flexWrap: 'wrap'
        }}>
          {CLIENT_SETTINGS.map(setting => this.renderField(setting))}
        </Form>
      </div>
    )
  }
}
