import React, { Component } from 'react'
import GetGridOverlay from './GetGridOverlay'
import VersionList from './VersionList'
import Controls from './Controls'
import ClientConfiguration from './ClientConfiguration'
import CommandLineSnippet from '../../../widgets/CommandLineSnippet'
import { Row } from '../../../widgets/Row'

class ClientItem extends Component {
  render() {
    const { client } = this.props
    const selected = client.name === 'Ganache'
    return (
      <div style={{
        width: 100,
        height: 100,
        padding: 5,
        margin: 5,
        borderWidth: selected ? 2 : 1,
        borderStyle: 'solid',
        borderColor: selected ? '#08a79c' : 'black',
        borderRadius: 10,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <img
          style={{
            maxWidth: '100%',
            height: 'auto',
            maxHeight: '100%'
          }}
          src={client.logo} />
      </div>
    )
  }
}

export default class Grid extends Component {
  renderMainPanel = () => {
    const clients = [
      { name: 'Ganache', logo: 'https://raw.githubusercontent.com/trufflesuite/ganache/develop/static/icons/png/128x128.png' },
      { name: 'Geth', logo: 'https://geth.ethereum.org/static/images/mascot.png' },
      { name: 'Besu', logo: 'https://www.hyperledger.org/wp-content/uploads/2019/08/Hyperledger_Besu_color.png' },
      { name: 'Nethermind', logo: 'https://c.gitcoin.co/grants/c5141dbcabe3cca0d61c76d9e7e3b6a7/Nethermind_logo_RGB.png' },
      { name: 'Open Ethereum', logo: 'https://www.parity.io/assets/img/logos/logo-parity-dark.png?v=668139a6b7' }
    ]
    return (
      <div style={{
        maxHeight: '100%'
      }}>
        <Row style={{ justifyContent: 'normal' }}>
          {clients.map(client => <ClientItem key={client.name} client={client} />)}
        </Row>
        <h2>Client version</h2>
        <VersionList />
        <h2>Client config</h2>
        <ClientConfiguration />
        <h2>Flags preview</h2>
        <CommandLineSnippet command="xyz" />
        {/*
        <h2>Client controls</h2>
        <Controls style={{
          marginTop: 20
        }} />
         */}
        {
          /* TODO  Disk Info */
        }
      </div>
    )
  }
  render() {
    const gridRunning = true
    return (
      <div style={{
        position: 'relative',
      }}>
        {gridRunning
          ? this.renderMainPanel()
          : <GetGridOverlay />
        }
      </div>
    )
  }
}
